<template id="admin-list">
  <div class="contentlist">
    <div class="flex-container" >

      <div class="columns m-t-10">
        <div class="column">
          <h1 class="title">Pasien</h1>
        </div>
        <div class="column" >
            <v-flex sm6 d-flex style="margin-left: auto">
              <v-btn slot="activator" color="blue lighten-2" dark @click.prevent="editDialog = true; typeInput = 'new'">Tambah Pasien</v-btn>
            </v-flex>
        </div>
      </div>
       <nav class="level">
             <div class="level-left">
                <b-field grouped group-multiline>
                    <b-select v-model="perPage" :disabled="!isPaginated">
                        <option value="10">10 per page</option>
                        <option value="20">20 per page</option>
                        <option value="50">50 per page</option>
                    </b-select>
                </b-field>
             </div>
             <div class="level-right">
                    <b-field>
                        <b-input placeholder="Search..."
                            type="search"
                            icon="magnify"
                            v-model="search">
                        </b-input>
                    </b-field>
                 </div>
           </nav>
        <b-table :data="patiensList" :paginated="true" :per-page="perPage" :current-page.sync="currentPage" :loading="loadData" :pagination-simple="true" :narrowed="true" :mobile-cards="true" :striped="true" :hoverable="true" :default-sort-direction="defaultSortDirection" default-sort="updated_at">
            <template slot-scope="props">
                <b-table-column label="No." sortable>{{ props.index + 1 }}</b-table-column>
                <b-table-column field="name" label="Nama">{{ props.row.name }}</b-table-column>
                <b-table-column field="username" label="Username">{{ props.row.username }}</b-table-column>
                <b-table-column field="detail.nik" label="NIK">{{ props.row.detail.nik }}</b-table-column>
                <b-table-column field="detail.placeOfBirth" label="Tempat Lahir">{{ props.row.detail.placeOfBirth }}</b-table-column>
                <b-table-column field="detail.dateOfBirth" label="Tanggal Lahir">{{ props.row.detail.dateOfBirth }}</b-table-column>
                <b-table-column field="detail.gender" label="Jenis Kelamin">{{ props.row.detail.gender }}</b-table-column>
                <b-table-column field="detail.bloodType" label="Golongan Darah">{{ props.row.detail.bloodType }}</b-table-column>
                <b-table-column field="detail.address" label="Alamat">{{ props.row.detail.address }}</b-table-column>
                <b-table-column field="detail.association" label="RT/RW">{{ props.row.detail.association }}</b-table-column>
                <b-table-column field="detail.village" label="Desa">{{ props.row.detail.village }}</b-table-column>
                <b-table-column field="detail.subdistrict" label="Kecamatan">{{ props.row.detail.subdistrict }}</b-table-column>
                <b-table-column field="detail.religion" label="Agama">{{ props.row.detail.religion }}</b-table-column>
                <b-table-column field="detail.maritalStatus" label="Status Perkawinan">{{ props.row.detail.maritalStatus }}</b-table-column>
                <b-table-column field="detail.job" label="Pekerajaan">{{ props.row.detail.job }}</b-table-column>
                <b-table-column field="detail.citizenship" label="Kewarganegaraan">{{ props.row.detail.citizenship }}</b-table-column>
                <b-table-column field="created_at" label="Mendaftar Pada">{{ props.row.created_at }}</b-table-column>
                            <b-table-column label=""><v-menu transition="slide-x-transition" offset-x left>
                                <v-btn slot="activator" icon >
                                <v-icon>more_vert</v-icon>
                                </v-btn>
                            <v-list>
                            <v-list-tile  @click.prevent="seteditData(props.row); editDialog = true; typeInput = 'edit'">
                                <v-list-tile-title  >Ubah</v-list-tile-title>
                            </v-list-tile>
                            <v-list-tile @click.prevent="deleteId = props.row.id; deleteDialog = true">
                                <v-list-tile-title >Hapus</v-list-tile-title>
                            </v-list-tile>
                            </v-list>
                        </v-menu>
                        </b-table-column>   
                      </template>
                        <template slot="empty">
                          <section class="section">
                          <div class="content has-text-grey has-text-centered">
                              <p>
                                <b-icon
                                    icon="emoticon-sad"
                                    size="is-large">
                                </b-icon>
                        </p>
                        <p>Belum ada data pasien, silahkan tambahkan pasien terlebih dahulu.</p>
                    </div>
                </section>
            </template>
        </b-table>
      
       <v-snackbar
          v-model="snackbar"
          :color="color"
          :multi-line="true"
          :timeout="3000"
        >
          {{ text }}
          <v-btn
            dark
            flat
            @click="snackbar = false"
          >
            Close
          </v-btn>
        </v-snackbar>

        <!-- edit -->
         <v-layout row justify-center>
           <v-form
                ref="form"
                lazy-validation
            >
            <v-dialog v-model="editDialog" persistent max-width="600px">
              <v-card>
                <v-card-title>
                    Pasien
                </v-card-title>
                <v-card-text>
                  <v-container grid-list-md>
                    <v-layout wrap>
                <v-flex xs12 sm6>
                  <v-text-field
                    prepend-inner-icon="perm_contact_calendar"
                    v-model="editData.nik"
                    required
                    label="NIK"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 sm6>
                  <v-text-field
                    prepend-inner-icon="keyboard_arrow_down"
                    v-model="editData.placeOfBirth"
                    required
                    label="Tempat Lahir"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field
                    prepend-inner-icon="text_rotation_none"
                    v-model="editData.name"
                    required
                    label="Nama"
                  ></v-text-field>
                </v-flex>
                    <v-flex xs12>
                        Tanggal Lahir
                      </v-flex>
                      <v-flex xs4>
                          <v-select
                            :items="listDate"
                            v-model="date"
                            label="Tanggal"
                        ></v-select>
                      </v-flex>
                      <v-flex xs4>
                        <v-select
                            :items="listMonth"
                            item-value="number"
                            item-text="text"
                            v-model="month"
                            label="Bulan"
                        ></v-select>
                      </v-flex>
                      <v-flex xs4>
                        <v-select
                            :items="listYear"
                            v-model="year"
                            label="Tahun"
                        ></v-select>                          
                      </v-flex>

                <v-flex xs12>
                  <v-text-field
                    prepend-inner-icon="room"
                    required
                    label="Alamat"
                    v-model="editData.address"
                  ></v-text-field>
                </v-flex>
                <v-flex xs4>
                  <v-text-field
                    prepend-inner-icon="group"
                    required
                    label="RT/RW"
                    v-model="editData.association"
                  ></v-text-field>
                </v-flex>
                <v-flex xs4>
                  <v-text-field
                    prepend-inner-icon="contacts"
                    required
                    label="Kel/Desa"
                    v-model="editData.village"                  
                  ></v-text-field>
                </v-flex>
                <v-flex xs4>
                  <v-text-field
                    prepend-inner-icon="contact_mail"
                    required
                    label="Kecamatan"
                    v-model="editData.subdistrict"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 sm6>
                  <v-text-field
                    prepend-inner-icon="contact_mail"
                    required
                    label="Agama"
                    v-model="editData.religion"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 sm6>
                <v-autocomplete
                  prepend-inner-icon="nature_people"
                  :items="['Belum Kawin','Sudah Kawin']"
                  label="Status Kawin"
                  v-model="editData.maritalStatus"
                ></v-autocomplete>
              </v-flex>
                <v-flex xs6>
              <v-text-field
                    prepend-inner-icon="bookmarks"
                    required
                    label="Pekerjaan"
                    v-model="editData.job"
                  ></v-text-field>
              </v-flex>
                <v-flex xs6>
                <v-autocomplete
                  prepend-inner-icon="landscape"
                  :items="['Wni','Wna']"
                  label="Kependudukan"
                  v-model="editData.citizenship"
                ></v-autocomplete>
              </v-flex>
                <v-flex xs6>
                <v-autocomplete
                  prepend-inner-icon="account"
                  :items="['Laki-laki','Perempuan']"
                  label="Jenis Kelamin"
                  v-model="editData.gender"
                ></v-autocomplete>
              </v-flex>
                <v-flex xs6>
                <v-autocomplete
                  prepend-inner-icon="landscape"
                  :items="['A','B','O','AB']"
                  label="Golongan Darah"
                  v-model="editData.bloodType"
                ></v-autocomplete>
              </v-flex>
                    </v-layout>
                  </v-container>
                  <small>*indicates required field</small>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="red darken-1" dark @click="resetData()">Batal</v-btn>
                  <v-btn color="blue darken-1" v-if="typeInput == 'new'" dark @click.prevent="sendData()" :loading="load">Tambahkan</v-btn>
                  <v-btn color="blue darken-1" v-if="typeInput == 'edit'" dark @click.prevent="UpdateData()" :loading="load">Update</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
           </v-form>
          </v-layout>
        <!-- edit -->
        <!-- delete -->
          <v-layout row justify-center>
                  <v-dialog v-model="deleteDialog" persistent max-width="290">
                    <v-card>
                      <v-card-title class="headline">Hapus Pasien ?</v-card-title>
                      <v-card-text>Pasien yang dihapus akan juga menghapus seluruh data didalamnya dan tidak dapat dikembalikan lagi.</v-card-text>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="green darken-1" dark @click="resetData()">Batal</v-btn>
                        <v-btn color="red darken-1" dark :loading="load" @click="deleteData()">Hapus</v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </v-layout>
        <!-- delete -->
              </div>
              </div>
              </template>

<script>
export default {
  mounted () {
    this.getPatien()
  },
  data () {
    return {
      date: 1,
      month: 1,
      year: 1998,
      listMonth: [
        {number: 1, text: 'Januari'},
        {number: 2, text: 'Februari'},
        {number: 3, text: 'Maret'},
        {number: 4, text: 'April'},
        {number: 5, text: 'Mei'},
        {number: 6, text: 'Juni'},
        {number: 7, text: 'July'},
        {number: 8, text: 'Agustus'},
        {number: 9, text: 'September'},
        {number: 10, text: 'Oktober'},
        {number: 11, text: 'November'},
        {number: 12, text: 'Desember'}
      ],
      typeInput: 'new',
      role: '',
      stat: '',
      search: '',
      table: 0,
      users: [],
      dialog: false,
      load: false,
      deleteDialog: false,
      editDialog: false,
      snackbar: false,
      text: '',
      color: null,
      reset: false,
      editData: {},
      deleteId: -1,
      loading: false,
      loadData: true,
      isPaginated: true,
      isPaginationSimple: true,
      defaultSortDirection: 'desc',
      currentPage: 1,
      perPage: 10,
      roles: [],
      patiens: []
    }
  },
  computed: {
    patiensList () {
      if (this.patiens.length) {
        return this.patiens.filter((row, index) => {
          if (this.search !== '') return row.name.toLowerCase().includes(this.search.toLowerCase())
          return true
        })
      }
    },
    listYear () {
      var year = []
      var today = new Date()
      var thisYear = today.getFullYear()
      var limitAge = 5
      var startYear = thisYear - limitAge
      for (let i = 0; i < 20; i++) {
        year.push(startYear - i)
      }
      return year
    },
    listDate () {
      var date = []
      for (let i = 1; i < 32; i++) {
        date.push(i)
      }
      return date
    }
  },
  methods: {
    resetData () {
      this.deleteDialog = false
      this.deleteId = -1
      this.$refs.form.reset()
      this.editDialog = false
    },
    getPatien () {
      this.editDialog = false
      this.deleteDialog = false
      this.load = false
      var uri
      var config = {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
      }
      uri = this.$apiUrl + 'patien'
      this.$http.get(uri, config).then(response => {
        this.$refs.form.reset()
        this.patiens = response.data
        for (let i = 0; i < this.patiens.length; i++) {
          if (this.patiens[i].detail == null) {
            this.patiens[i].detail = {
              nik: '',
              placeOfBirth: '',
              dateOfBirth: '',
              gender: '',
              bloodType: '',
              address: '',
              association: '',
              village: '',
              subdistrict: '',
              religion: '',
              maritalStatus: '',
              job: '',
              citizenship: ''
            }
          }
        }
        this.loadData = false
      })
    },
    sendData () {
      this.load = true
      var uri
      var config = {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
      }
      uri = this.$apiUrl + 'patien'
      var lastDay = new Date(this.year, this.month + 1, 0)
      if (this.date > lastDay) {
        this.date = lastDay
      }
      this.editData.dateOfBirth = this.year + '-' + this.month + '-' + this.date
      this.$http.post(uri, this.editData, config).then(response => {
        console.log(response)
        this.getPatien()
      }).catch(error => {
        console.log(error)
        this.snackbar = true
        this.text = 'Coba lagi'
        this.color = 'error'
        this.load = false
      })
    },
    seteditData (data) {
      this.editData = {
        id: data.id,
        name: data.name,
        nik: data.detail.nik,
        placeOfBirth: data.detail.placeOfBirth,
        dateOfBirth: data.detail.dateOfBirth,
        gender: data.detail.gender,
        bloodType: data.detail.bloodType,
        address: data.detail.address,
        association: data.detail.association,
        village: data.detail.village,
        subdistrict: data.detail.subdistrict,
        religion: data.detail.religion,
        maritalStatus: data.detail.maritalStatus,
        job: data.detail.job,
        citizenship: data.detail.citizenship
      }
      if (data.detail.dateOfBirth !== '') {
        var d = new Date(data.detail.dateOfBirth)
        this.date = d.getDate()
        this.month = parseInt(d.getMonth()) + 1
        this.year = d.getFullYear()
      }
    },
    deleteData () {
      this.load = true
      var config = {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
      }
      var uri = this.$apiUrl + 'patien/' + this.deleteId
      this.$http.delete(uri, config).then(response => {
        this.snackbar = true
        this.text = 'Data berhasil dihapus'
        this.color = 'green'
        this.getPatien()
      }).catch(error => {
        console.log(error)
        this.snackbar = true
        this.text = 'Coba Lagi'
        this.color = 'red'
        this.load = false
      })
    },
    UpdateData () {
      this.load = true
      var uri
      var config = {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
      }
      this.editData.dateOfBirth = this.year + '-' + this.month + '-' + this.date
      uri = this.$apiUrl + 'patien/' + this.editData.id
      this.$http.post(uri, this.editData, config).then(response => {
        console.log(response)
        this.getPatien()
      }).catch(error => {
        console.log(error)
        this.getPatien()
        this.snackbar = true
        this.text = 'try again'
        this.color = 'error'
      })
    }
  }
}
</script>



